<template>
  <div id="fixed-float-window">
    <Title :title="''"/>

    <TextInput :title="''" :placeholder="''" v-model="componentName" :maxLength="16" required noSymbol/>
    <ErrorMsg :message="validateComponentName" v-if="validateComponentName&&showValidationMsg"/>

    <ImageUploader :imgPrefix="'FixedFloatWindow'" :fileName="backgroundImgName" @success="uploadImageSuccess" @remove="removeImg"/>
    <ErrorMsg :message="validteBackgroundImg" v-if="validteBackgroundImg&&showValidationMsg"/>

    <TextInput :title="''" :hint="'http://https://'" :placeholder="''" v-model="link" required/>
    <ErrorMsg :message="validteLink" v-if="validteLink&&showValidationMsg"/>

    <RedictWay v-model="way"/>
    <ButtonGroup @buttonConfirmed="confirm" @buttonCanceled="cancel" />
  </div>
</template>

<script>
import Title from '../form-item/Title'
import ButtonGroup from '../form-item/ButtonGroup'
import TextInput from '../form-item/TextInput'
import ImageUploader from '../form-item/ImageUploader'
import RedictWay from '../form-item/RedictWay'
import ErrorMsg from '../form-item/ErrorMsg'
import { mapState } from 'vuex'

export default {
  name: 'FixedFloatWindow',
  components: {
    Title,
    ButtonGroup,
    TextInput,
    ImageUploader,
    RedictWay,
    ErrorMsg
  },
  data () {
    return {
      showValidationMsg: false,

      componentName: '',
      backgroundImgName: '',
      backgroundImgUrlRel: '',
      link: '',
      way: null
    }
  },
  watch: {
    componentName () {
      this.commit({ componentName: this.componentName })
    },
    backgroundImgUrlRel () {
      this.commit({ backgroundImgUrlRel: this.backgroundImgUrlRel, backgroundImgName: this.backgroundImgName })
    },
    link () {
      this.commit({ link: this.link })
    },
    way () {
      this.commit({ way: this.way })
    }
  },
  computed: {
    ...mapState({
      fixedFloatingWindow: state => state.fixedFloatingWindow,
    }),
    validateComponentName () {
      const error = !this.componentName ? '' : ''
      return error
    },
    validteBackgroundImg () {
      const error = !this.backgroundImgUrlRel ? '' : ''
      return error
    },
    validteLink () {
      let error = ''
      if (!this.link) {
        error = ''
      } else if (!this.link.startsWith('http://') && !this.link.startsWith('https://')) {
        error = ''
      }
      return error
    },
    validated () {
      return !this.validateComponentName && !this.validteBackgroundImg && !this.validteLink
    }
  },
  methods: {
    uploadImageSuccess (res) {
      this.backgroundImgName = res.fileName
      this.backgroundImgUrlRel = res.data.RelativePath
    },
    removeImg () {
      this.backgroundImgName = ''
      this.backgroundImgUrlRel = ''
    },
    commit (payload) {
      this.$store.commit('changeFixedFloatWindow', payload)
    },
    confirm () {
      if (this.validated) {
        this.$store.commit('save')
        this.$store.commit('changeEditArea', '')
      }
      this.showValidationMsg = true
    },
    cancel () {
      this.$store.commit('rollback')
      this.$store.commit('changeEditArea', '')
    },
  },
  created () {
    const data = this.fixedFloatingWindow
    this.componentName = data.componentName,
    this.backgroundImgName = data.backgroundImgName
    this.backgroundImgUrlRel = data.backgroundImgUrlRel,
    this.link = data.link,
    this.way = data.way
  }
}
</script>

<style scoped lang="stylus">
  #fixed-float-window
    display flex
    flex-direction column
    padding 0 15px 20px
</style>