<template>
  <div id="float-text">
    <Title :title="''"/>
    <div class="form">
      <TextInput :title="''" :placeholder="''" required noSymbol :maxLength="16" v-model="componentName"/>
      <ErrorMsg :message="validateComponentName" v-if="validateComponentName&&showValidationMsg"/>
      <ImageUploader :title="''" :fileName="backgroundImgName" :imgPrefix="'FloatText'" :required="false" @success="uploadImageSuccess" @remove="removeImg"/>
      <BackgroundColor v-model="backgroundColor" :opacity.sync="backgroundOpacity"/>
      <TextInput :title="''" :placeholder="''" :hint="'16'" required noSymbol :maxLength="16" v-model="text"/>
      <ErrorMsg :message="validateText" v-if="validateText&&showValidationMsg"/>
      <FontSize v-model="fontSize"/>
      <FontColor v-model="fontColor"/>
      <div class="show-text-toggle">
        <a-checkbox @change="onChange" :checked="show"></a-checkbox><span style="color: #868686"></span>
      </div>
    </div>
    <ButtonGroup @buttonConfirmed="confirm" @buttonCanceled="cancel" :notes="notes"/>
  </div>
</template>

<script>
import Title from '../form-item/Title'
import ButtonGroup from '../form-item/ButtonGroup'
import TextInput from '../form-item/TextInput'
import FontSize from '../form-item/FontSize'
import FontColor from '../form-item/FontColor'
import ImageUploader from '../form-item/ImageUploader'
import BackgroundColor from '../form-item/BackgroundColor'
import ErrorMsg from '../form-item/ErrorMsg'
import { mapState } from 'vuex'

export default {
  name: 'FloatText',
  components: {
    Title,
    ButtonGroup,
    TextInput,
    FontSize,
    FontColor,
    ImageUploader,
    BackgroundColor,
    ErrorMsg
  },
  data () {
    return {
      showValidationMsg: false,
      notes: '1.2.*',
      
      componentName: '',
      backgroundImgName: '',
      backgroundImgUrlRel: '',
      backgroundColor: '',
      backgroundOpacity: 0,
      text: '',
      fontSize: 0,
      fontColor: '',
      show: true,
    }
  },
  watch: {
    output () {
      this.commit()
    }
  },
  computed: {
    ...mapState({
      floatText: state => state.floatText,
    }),
    output () {
      const { componentName, backgroundImgName, backgroundImgUrlRel, backgroundColor, backgroundOpacity, text, fontSize, fontColor, show } = this
      return { componentName, backgroundImgName, backgroundImgUrlRel, backgroundColor, backgroundOpacity, text, fontSize, fontColor, show }
    },
    validateComponentName () {
      const error = !this.componentName ? '' : ''
      return error
    },
    validateText () {
      const error = !this.text ? '' : ''
      return error
    },
    validated () {
      return !this.validateText && !this.validateComponentName
    }
  },
  methods: {
    uploadImageSuccess (res) {
      this.backgroundImgName = res.fileName
      this.backgroundImgUrlRel = res.data.RelativePath
    },
    removeImg () {
      this.backgroundImgName = ''
      this.backgroundImgUrlRel = ''
    },
    onChange (evt) {
      this.show = evt.target.checked
    },
    commit () {
      this.$store.commit('changeFloatText', this.output)
    },
    confirm () {
      if (this.validated) {
        this.$store.commit('save')
        this.$store.commit('changeEditArea', '')
      }
      this.showValidationMsg = true
    },
    cancel () {
      this.$store.commit('rollback')
      this.$store.commit('changeEditArea', '')
    },
  },
  created () {
    const data = this.floatText
    this.componentName = data.componentName,
    this.backgroundImgName = data.backgroundImgName,
    this.backgroundImgUrlRel = data.backgroundImgUrlRel,
    this.backgroundColor = data.backgroundColor,
    this.backgroundOpacity = data.backgroundOpacity,
    this.text = data.text,
    this.fontSize = data.fontSize,
    this.fontColor = data.fontColor,
    this.show = data.show
  }
}
</script>

<style scoped lang="stylus">
  #float-text
    display flex
    flex-direction column
    padding 0 15px 20px
  
  .form
    max-height 500px
    overflow-x hidden
    overflow-y auto

  .show-text-toggle
    padding 0.5rem 5px

</style>